.gift-load-error {
  background-color: $text-background-color !important;
  background-image: url('../images/white-paperboard.png');
  background-repeat: repeat;

  position: relative;
  margin: auto;
  max-width: 960px;
  padding: 24px;
  line-height: 2em;
}

.gift-full-error {
  font-size: smaller;
  max-height: 500px;
  overflow: scroll;
}

.envelope-text {
  p, h1, h2, h3, h4, h5, h6, table {
    margin-left: 5px;
    margin-right: 5px;

    img {
      margin-left: -5px;
      margin-right: -5px;
    }
  }
}
